import { Component, OnInit } from '@angular/core';
import { WebsocketchatService } from 'websocketchatlib';
import { ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit {

  msgList: Array<any> = new Array<any>();
  msg: string;
  toUser: string;
  errmsg: string = null;
  constructor(
    private websocketService: WebsocketchatService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
  ) {
    this.websocketService.websocket.onmessage = (msg: any) => {
      console.log("收到消息：");
      console.log(msg);
      this.msgList.push(JSON.parse(msg.data));
    };
  }

  ngOnInit() {
    this.toUser=this.activatedRoute.snapshot.params['id'];
  }

  onmsg(msg: any) {
    console.log("收到消息：");
    console.log(msg);
    this.msgList.push(JSON.parse(msg.data));
  }

  send() {
    this.errmsg = null;
  this.websocketService.sendMsg(this.msg, this.websocketService.user.userId, this.toUser)
    .then((msg: any) => {
      if (!msg) {
        this.errmsg = "用户未登录，无法接收信息";
        return;
      }
      this.msgList.push(msg);
      this.msg = null;
    }).catch((msg: any) => {
      this.errmsg = "用户未登录，无法接收信息";
    })
  }

  back() {
    this.router.navigate(['/users']);
  }

}
