import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { WebsocketchatlibModule } from 'websocketchatlib';
import { WebsocketchatService } from 'websocketchatlib';
import { AppComponent } from './app.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { MatDividerModule, MatToolbarModule, MatIconModule, MatButtonModule, MatCheckboxModule, MatInputModule, MatListModule, MatDialogModule, MatProgressSpinnerModule, MatProgressBarModule, MatMenuModule} from '@angular/material';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './login/login.component';
import {MatCardModule} from '@angular/material/card';
import { FormsModule }   from '@angular/forms';
import { LoadingComponent } from './loading/loading.component';
import { ChatComponent } from './chat/chat.component';
import { SelectuserComponent } from './selectuser/selectuser.component';
const appRoutes: Routes = [
  {
    path: 'login', component: LoginComponent
  },{
    path: 'chat', component: ChatComponent
  },{
    path: 'users', component: SelectuserComponent
  },
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    LoadingComponent,
    ChatComponent,
    SelectuserComponent
  ],
  imports: [
    BrowserModule,
    WebsocketchatlibModule,
    BrowserAnimationsModule,
    FormsModule,
    MatButtonModule,
    MatCheckboxModule,
    MatInputModule,
    MatCardModule,
    MatListModule,
    MatDialogModule,
    MatProgressSpinnerModule,
    MatProgressBarModule,
    MatIconModule,
    MatToolbarModule,
    MatMenuModule,
    MatDividerModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: false } // <-- debugging purposes only
    )
  ],
  providers: [WebsocketchatService],
  bootstrap: [AppComponent],
  entryComponents:[
    LoadingComponent
  ]
})
export class AppModule { }
